.team {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem ;
    color: white;
    background-image: linear-gradient( 109.6deg,  rgba(39,142,255,1) 11.2%, rgba(98,113,255,0.78) 100.2% );
  }

.team .bigContainer {
  margin-top: 3rem;
}

.profileContainer {
  display: flex;
  flex-direction: row;
  width: 75vw;
}

.team img {
  height: 25vh;
  width: auto;
  border-radius: 50%;
  margin: 2rem 3rem 5rem 3rem;
  transition: 450ms;
}

.team .ferry {
  border-radius: 50%;
}

.team img:hover {
  transform: scale(1.05) translateY(-10px);
  opacity: 0.8;
  transition: 150ms;
}

.logo {
  margin-left: auto;
}

p {
  text-align: center;
  margin-top: auto;
  margin-bottom: auto;
}