.datahub {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  color: #ffffff;
  background-image: linear-gradient( 109.6deg,  rgba(39,142,255,1) 11.2%, rgba(98,113,255,0.78) 100.2% );
  font-size: 1.2rem;
  height: max-content;
  width: fit-content;
}

.datahub h1 {
  width: max-content;
}

h3 {
  width: max-content;
  margin-bottom: 2rem;
}

.datahub .bigContainer {
    padding-top: 3rem;
    display: flex;
    font-size: 2.5vh;
    width: 100%;
    height: max-content;
    flex-direction: column;
    align-items: center;
  }

.datahub .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
  margin-bottom: 2rem;
  padding-bottom: 5rem;
  border-bottom: dashed whitesmoke 5px;
}
  
.datahub p {
    margin-top: 2rem;
    width: 100%;
}

  @keyframes onLoad {
    0% {
      opacity: 0;
      transform: translateY(-100%);
    }
    100% {
      transform: translateY(0), opacity 1;
    }
  }

  .datahub .animation {
    animation: 5s ease-out 0s 1 onLoad
  }
  
  .datahub .chartContainer {
    display: flex;
    justify-content: center;
    margin-top: 3rem;
    width: max-content;
    background-color: white;
    border: solid white 30px;
    border-radius: 50px;
  }

  .datahub .metrics-container{
    display: flex;
    flex-direction: row;
    width: 95vw;
    justify-content: space-evenly;
    flex-wrap: wrap;
  }

  .datahub .card-container{
    width: 20vw;
    height: max-content;
    transition: height 1s;
    text-align: center;
  }

  .cases:hover .casesList, .disparities:hover .disparitiesList, .mortality:hover .mortalityList, .vaccinations:hover .vaccinationsList {
    visibility: visible;
    background-color: rgba(20, 43, 92, 0.4);
    transition-timing-function: ease-out;
    transition: 350ms;
  }

  .cases:hover > h2, .disparities:hover > h2, .mortality:hover > h2, .vaccinations:hover > h2 {
    border-radius: 25px 25px 0 0;
  }

  .datahub h2 {
    background-color: rgba(20, 43, 92, 0.4);
    border-radius: 25px 25px 25px 25px;
  }

  .datahub .card-list {
    display: flex;
    visibility: hidden;
    margin-left: auto;
    flex-direction: column;
    align-items: center;
    list-style-type: none;
    text-align: center;
    transition: 100ms;
    border-radius: 0 0 25px 25px;
  }

  .datahub .pushable {
    margin: 1rem 1rem;
    position: relative;
    border: none;
    background: transparent;
    width: max-content;
    padding: 0;
    cursor: pointer;
    outline-offset: 4px;
    transition: filter 250ms;
  }
  .datahub .shadow {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 12px;
    background: hsl(0deg 0% 0% / 0.25);
    will-change: transform;
    transform: translateY(2px);
    transition:
      transform
      600ms
      cubic-bezier(.3, .7, .4, 1);
  }
  
  .datahub .front {
    display: block;
    position: relative;
    padding: 1rem 2rem;
    border-radius: 12px;
    font-size: 1rem;
    color: white;
    width: max-content;
    background: hsl(345deg 93% 47%);
    will-change: transform;
    transform: translateY(-4px);
    transition:
      transform
      600ms
      cubic-bezier(.3, .7, .4, 1);
  }

  .datahub a {
    text-decoration: none;
  }

  .datahub .pushable:hover {
    filter: brightness(110%);
  }
  .datahub .pushable:hover .front {
    transform: translateY(-6px);
    transition:
      transform
      250ms
      cubic-bezier(.3, .7, .4, 1.5);
  }
  .datahub .pushable:active .front {
    transform: translateY(-2px);
    transition: transform 34ms;
  }
  .datahub .pushable:hover .shadow {
    transform: translateY(4px);
    transition:
      transform
      250ms
      cubic-bezier(.3, .7, .4, 1.5);
  }
  .datahub .pushable:active .shadow {
    transform: translateY(1px);
    transition: transform 34ms;
  }
  .datahub .pushable:focus:not(:focus-visible) {
    outline: none;
  }

  .datahub #tutorial {
    margin-bottom: 5rem;
    width: 50%;
  }

  .datahub .tutorialContainer {
    margin-top: 3rem;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 100vw;
  }

  .datahub .tutorialText {
    margin: 1rem;
    font-size: 1.5rem;
    width: 100%;
  }

  .datahub .textContainer {
    display: flex;
    flex-direction: column;
    align-self: center;
    width: 40%;
  }