
.faq{
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  color: rgb(116, 153, 170);
  background-image: linear-gradient( 109.6deg,  rgba(39,142,255,1) 11.2%, rgba(98,113,255,0.78) 100.2% );
  font-size: 1.2rem;
  height: max-content;
}

ol, ul {
  list-style: none;
}

.faqNav {
  display: flex;
  flex-direction: column;
  flex-basis: content;
  align-self: flex-end;
  margin-left: 70%;
  margin-right: 3%;
  margin-top: 3%;
  position: fixed;
}

@media (max-width: 1000px) {
 .aboutNav, .faqNav {
    align-self: center;
    position: static;
    margin-right: 15.4%;
  }
}

.faq .faqNav-menu {
  list-style-type: none;
  background-color: #ffffff;
  width: 110%;
  border-radius: 25px
}

.faq .faqNav-item {
  display: flex;
  justify-content: center;
  color: rgb(98, 128, 146);
  font-size: 0.75rem;
  padding: 0.2rem;
}

.faq #bottomItem {
  border-radius: 0 0 25px 25px;
}

.faq .menuHeader {
  background-color: #86c2d1;
  color: rgb(255, 255, 255);
  border-radius: 20px 20px 0 0;
}

.faq .faqNav-link {
  text-decoration: none;
  color: rgb(86, 118, 155);
}

:not(.menuHeader).faqNav-item:hover {
  background-color: #65caf1;
  transition: 350ms;
}

.faq h1{
  margin-top: 1.5rem;
  margin-bottom: 2rem;
}

.faq p {
  margin-bottom: 3rem;
  text-align: center;
  line-height: 2.3rem;
}

.faq .textBox, .faq .faqNav-menu {
  -webkit-box-shadow: 10px 10px 36px -5px rgba(120,145,184,1);
  -moz-box-shadow: 10px 10px 36px -5px rgba(120,145,184,1);
  box-shadow: 10px 10px 36px -5px rgba(120,145,184,1);
}

.faq .textBox:hover, .faq .faqNav-menu:hover {
  -webkit-box-shadow: 10px 10px 36px -5px rgb(56, 84, 128);
  -moz-box-shadow: 10px 10px 36px -5px rgb(56, 84, 128);
  box-shadow: 10px 10px 36px -5px rgb(56, 84, 128);
  transition: 400ms ease-in;
}

.faq .segments {
  display: flex;
  width: 100%;
  flex-direction: column;
}

.faq .container {
  display: flex;
  flex-direction: column;
  align-self: center;
  align-items: center;
  width: 50%;
  margin-top: 2rem;
  margin-bottom: 2rem;
  padding-bottom: 5rem;
  border-bottom: dashed whitesmoke 5px;
}

.faq .faqLink {
  color: #4e73b8;
  text-decoration: none;
  font-style: bold; 
}

.faq .faqLink:hover {
  color: #359ebe;
  transition: 400ms
} 

.faq .textBox {
  padding-right: 1.5rem;
  padding-left: 1.5rem;
  padding-top: 1.5rem;
  margin-bottom: 1rem;
  background-color: white;
  border-radius: 1.5rem;
  min-width: 450px;
}

.faq .sectionTitle {
  margin-bottom: 3rem;
}

.faq .adjCounties {
  margin-left:5rem;
  margin-bottom: 2rem;
}