.universities {
    background-image: linear-gradient( 109.6deg,  rgba(39,142,255,1) 11.2%, rgba(98,113,255,0.78) 100.2% );
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    color: white;
    font-size: 2.5rem;
  }

  .universities h1 {
    margin-top: 3rem;
  }

  .universities #top {
    margin-top: 2rem;
    margin-bottom: 0;
  }
  
  .universities .segment {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 80%;
    margin-bottom: 3rem;
    margin-top: 4rem;
  }

  .universities p {
    width: 40%;
    margin-left: auto;
    margin-right: auto;
  }

  .universities .chartContainer {
    width: 100%;
  }
  
  .universities .circleChart {
    width: 40vw;
  }