@media (max-width: 1000px) {
    .about .aboutNav {
      align-self: center;
      position: static;
      margin-right: 15.4%;
    }
}

.about {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  background-image: linear-gradient( 109.6deg,  rgba(39,142,255,1) 11.2%, rgba(98,113,255,0.78) 100.2% );
  font-size: 1.2rem;
  height: max-content;
}

.about .aboutNav {
    flex-direction: column;
    position: fixed;
    margin-left: 80%;
    margin-right: 10%;
    margin-top: 3%;
    align-self: flex-end;
    z-index: 1;
  }

.about .aboutNav-menu {
  list-style-type: none;
  background-color: #ffffff;
  width: 150%;
  border-radius: 25px
}

.about .aboutNav-item {
    display: flex;
    justify-content: center;
    color: rgb(98, 128, 146);
    font-size: 1.5rem;
    padding: 0.2rem;
  }

  .about #bottomItem{
    border-radius: 0 0 25px 25px;
  }
  
  .about .menuHeader{
    background-color: #86c2d1;
    color: rgb(255, 255, 255);
    border-radius: 20px 20px 0 0;
  }
  
  .about .aboutNav-link{
    text-decoration: none;
    color: rgb(86, 118, 155);
  }
  
  :not(.menuHeader).aboutNav-item:hover{
    background-color: #65caf1;
    transition: 350ms;
  }
  
  .about h1{
    padding-top: 1rem;
  }
  
  .about p{
    margin-bottom: 3rem;
    text-align: center;
    color: rgb(116, 153, 170);;
    line-height: 2.3rem;
  }

  .about .segments{
    display: flex;
    width: 100%;
    flex-direction: column;
  }
  
  .about .container{
    display: flex;
    flex-direction: column;
    align-self: center;
    align-items: center;
    width: 50%;
    margin-top: 2rem;
    margin-bottom: 2rem;
    padding-bottom: 5rem;
    border-bottom: dashed whitesmoke 5px;
  }
  
  .about .aboutLink{
    color: #4e73b8;
    text-decoration: none;
    font-style: bold; 
  }
  
  .about .aboutLink:hover{
    color: #359ebe;
    transition: 400ms
  } 
  
  .about .textBox{
    padding-right: 1.5rem;
    padding-left: 1.5rem;
    padding-top: 1.5rem;
    margin-bottom: 1rem;
    background-color: white;
    border-radius: 1.5rem;
    min-width: 450px;
  }
  
  .about .sectionTitle{
    margin-bottom: 3rem;
  }

  .about .textBox, .about .aboutNav-menu {
    -webkit-box-shadow: 10px 10px 36px -5px rgba(120,145,184,1);
    -moz-box-shadow: 10px 10px 36px -5px rgba(120,145,184,1);
    box-shadow: 10px 10px 36px -5px rgba(120,145,184,1);
  }
  
  .about .textBox:hover, .about .aboutNav-menu:hover {
    -webkit-box-shadow: 10px 10px 36px -5px rgb(56, 84, 128);
    -moz-box-shadow: 10px 10px 36px -5px rgb(56, 84, 128);
    box-shadow: 10px 10px 36px -5px rgb(56, 84, 128);
    transition: 400ms ease-in;
  }