.data {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: max-content;
    background-image: linear-gradient( 109.6deg,  rgba(39,142,255,1) 11.2%, rgba(98,113,255,0.78) 100.2% );
  }
  
  .data .bigContainer {
    display: flex;
    flex-direction: column; 
    /*width: 65%;*/
    width: 65vw;
  }

  .data a {
    text-decoration: none;
  }

  .data img {
    margin-top: 3rem;
    border-radius: 25px;
  }

  .data .dataTableContainer {
    margin-top: 2rem;
    margin-bottom: 5rem;
    z-index: 0;
    width: 100%;
    max-height: max-content;
  }
  
  .data .buttonContainer {
    display: flex;
    flex-direction: row;
    align-self: center;
    justify-content: center;
    min-width: 350px;
    width: 100%;
    margin: 3rem 0rem 0rem 0rem;
  }

  @keyframes buttonIn {
    0% {opacity:0}
    30% {opacity: 0.3}
    50% {opacity: 0.5}
    70% {opacity: 0.7}
    100% {opacity:1}
  }
  
  .data .pushable {
    position: relative;
    border: none;
    background: transparent;
    padding: 0;
    width: max-content;
    margin: 0.5rem 0.5rem 0rem 0rem;
    cursor: pointer;
    outline-offset: 4px;
    transition: filter 250ms;
  }
  .data .shadow {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 12px;
    background: hsl(0deg 0% 0% / 0.25);
    will-change: transform;
    transform: translateY(2px);
    transition:
      transform
      600ms
      cubic-bezier(.3, .7, .4, 1);
  }
  
  .data .front {
    display: block;
    position: relative;
    padding: 1rem 2rem;
    border-radius: 12px;
    font-size: 1rem;
    color: white;
    width: 100%;
    height: 100%;
    background: hsl(345deg 93% 47%);
    will-change: transform;
    transform: translateY(-4px);
    transition:
      transform
      600ms
      cubic-bezier(.3, .7, .4, 1);
  }

  .data .footer {
    width: 80%;
    margin-top: 1rem;
    font-size: 1.5rem;
    color: white;
    border-top: solid white 1px;
  }

  .data .pushable:hover {
    filter: brightness(110%);
  }
  .data .pushable:hover .front {
    transform: translateY(-6px);
    transition:
      transform
      250ms
      cubic-bezier(.3, .7, .4, 1.5);
  }
.data .pushable:active .front {
    transform: translateY(-2px);
    transition: transform 34ms;
  }
.data .pushable:hover .shadow {
    transform: translateY(4px);
    transition:
      transform
      250ms
      cubic-bezier(.3, .7, .4, 1.5);
  }
.data .pushable:active .shadow {
    transform: translateY(1px);
    transition: transform 34ms;
  }
.data .pushable:focus:not(:focus-visible) {
    outline: none;
  }
.data .chartContainer {
    margin-bottom: 3.5rem;
    transition: transform 450ms;
  }

.data .chart {
    /*animation: 2.2s ease-out 0s 1 warpIn;*/
    transition: 450ms;
  }

.data .card-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
    font-size: 1.7rem;
    list-style-type: none;
    text-align: center;
  }

.data .card-item {
    margin: 1rem 0rem;
    background-color: whitesmoke;
  }

.data .card-container{
    position: fixed;
    align-self: flex-end;
    margin: 2rem;
    width: 10%;
  }

.data .return {
    margin-top: 2.5rem;
  }

.table table {
    border-collapse: collapse;
    background-color: white;
    color: black;
    font-size: 2.8rem;
    width: 100%;
    min-width: 400px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
  }

.table thead {
    background: rgba(8, 23, 56, 0.822);
    color: #ffffff;
    text-align: center;
  }

.table th, .table td {
    padding: 12px 15px
  }

 .table tbody tr {
    text-align: center;
  }

.table tbody tr:nth-of-type(even) {
    background-color: #f3f3f3;
}

@media only screen and (max-width: 900px) {
    .data .front {
      padding: 0.8rem;
      font-size: 0.8rem;
      width: max-content
  }
    .data .buttonContainer {
      margin: 3rem 0rem 0rem 6rem ;
    }

    .data .bigContainer {
      width: 65vw
    }
  }

  @media only screen and (max-width: 600px) {
    .data .front {
      padding: 0.65rem;
      font-size: 0.75rem;
  }
    .data .buttonContainer {
      margin: 3rem 0rem 0rem 1.5rem ;
    }

    .data .bigContainer {
      width: 90vw
    }

    .data .card-container {
      display: none;
    }
  }
@keyframes warpIn {
  0% {opacity: 0; transform: scale(0.85,0.85)}
  30% {opacity: 0.75; transform: scale(1,1)}
  50% {opacity: 1; transform: scale(1.03,1.03)}
  80% {transform: translateY(-10px)}
}

  .data .chart:hover {
    transition: transform 150ms;
    transform: scale(1.005)
  }