@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  box-sizing:border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  scroll-behavior: smooth;
}
.navbar {
    background-color: #8EC5FC;
    background-image: linear-gradient( 109.6deg,  rgba(39, 143, 255, 0.795) 11.2%, rgba(98,113,255,0.78) 100.2% );
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
    flex-basis: auto;
}

.navbar h1 {
    color: #f5f5f5;
    margin-left: 25%;
}

input {
    width: 10rem;
    height: 2rem;
    border-radius: 2rem;
    border-width: 0;
    text-align: center;
    margin-left: auto;
    margin-right: 3%;
    outline-width: 0;
}

.menu-bars {
    margin-left: 2rem;
    font-size:  2rem;
    background: none;
}

.nav-menu{
    /*background: rgba(171, 210, 238, 0.8);*/
    background-color: #8EC5FC;
    background-image: linear-gradient( 109.6deg,  rgba(39, 143, 255, 0.8) 11.2%, rgba(98,113,255,0.78) 100.2% );
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%; 
    transition: 850ms;
    z-index: 1;
}

.nav-menu.active {
    left: 0;
    transition: 500ms;
}

.nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}
.nav-text a:hover {
    background-color: #1a83ff;
    transition: 400ms;
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-image: rgba(39,142,255,1);
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span {
    margin-left: 16px;
}
.data {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: -webkit-max-content;
    height: max-content;
    background-image: linear-gradient( 109.6deg,  rgba(39,142,255,1) 11.2%, rgba(98,113,255,0.78) 100.2% );
  }
  
  .data .bigContainer {
    display: flex;
    flex-direction: column; 
    /*width: 65%;*/
    width: 65vw;
  }

  .data a {
    text-decoration: none;
  }

  .data img {
    margin-top: 3rem;
    border-radius: 25px;
  }

  .data .dataTableContainer {
    margin-top: 2rem;
    margin-bottom: 5rem;
    z-index: 0;
    width: 100%;
    max-height: -webkit-max-content;
    max-height: max-content;
  }
  
  .data .buttonContainer {
    display: flex;
    flex-direction: row;
    align-self: center;
    justify-content: center;
    min-width: 350px;
    width: 100%;
    margin: 3rem 0rem 0rem 0rem;
  }

  @-webkit-keyframes buttonIn {
    0% {opacity:0}
    30% {opacity: 0.3}
    50% {opacity: 0.5}
    70% {opacity: 0.7}
    100% {opacity:1}
  }

  @keyframes buttonIn {
    0% {opacity:0}
    30% {opacity: 0.3}
    50% {opacity: 0.5}
    70% {opacity: 0.7}
    100% {opacity:1}
  }
  
  .data .pushable {
    position: relative;
    border: none;
    background: transparent;
    padding: 0;
    width: -webkit-max-content;
    width: max-content;
    margin: 0.5rem 0.5rem 0rem 0rem;
    cursor: pointer;
    outline-offset: 4px;
    transition: -webkit-filter 250ms;
    transition: filter 250ms;
    transition: filter 250ms, -webkit-filter 250ms;
  }
  .data .shadow {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 12px;
    background: hsl(0deg 0% 0% / 0.25);
    will-change: transform;
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
    transition:
      -webkit-transform
      600ms
      cubic-bezier(.3, .7, .4, 1);
    transition:
      transform
      600ms
      cubic-bezier(.3, .7, .4, 1);
    transition:
      transform
      600ms
      cubic-bezier(.3, .7, .4, 1), -webkit-transform
      600ms
      cubic-bezier(.3, .7, .4, 1);
  }
  
  .data .front {
    display: block;
    position: relative;
    padding: 1rem 2rem;
    border-radius: 12px;
    font-size: 1rem;
    color: white;
    width: 100%;
    height: 100%;
    background: hsl(345deg 93% 47%);
    will-change: transform;
    -webkit-transform: translateY(-4px);
            transform: translateY(-4px);
    transition:
      -webkit-transform
      600ms
      cubic-bezier(.3, .7, .4, 1);
    transition:
      transform
      600ms
      cubic-bezier(.3, .7, .4, 1);
    transition:
      transform
      600ms
      cubic-bezier(.3, .7, .4, 1), -webkit-transform
      600ms
      cubic-bezier(.3, .7, .4, 1);
  }

  .data .footer {
    width: 80%;
    margin-top: 1rem;
    font-size: 1.5rem;
    color: white;
    border-top: solid white 1px;
  }

  .data .pushable:hover {
    -webkit-filter: brightness(110%);
            filter: brightness(110%);
  }
  .data .pushable:hover .front {
    -webkit-transform: translateY(-6px);
            transform: translateY(-6px);
    transition:
      -webkit-transform
      250ms
      cubic-bezier(.3, .7, .4, 1.5);
    transition:
      transform
      250ms
      cubic-bezier(.3, .7, .4, 1.5);
    transition:
      transform
      250ms
      cubic-bezier(.3, .7, .4, 1.5), -webkit-transform
      250ms
      cubic-bezier(.3, .7, .4, 1.5);
  }
.data .pushable:active .front {
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
    transition: -webkit-transform 34ms;
    transition: transform 34ms;
    transition: transform 34ms, -webkit-transform 34ms;
  }
.data .pushable:hover .shadow {
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
    transition:
      -webkit-transform
      250ms
      cubic-bezier(.3, .7, .4, 1.5);
    transition:
      transform
      250ms
      cubic-bezier(.3, .7, .4, 1.5);
    transition:
      transform
      250ms
      cubic-bezier(.3, .7, .4, 1.5), -webkit-transform
      250ms
      cubic-bezier(.3, .7, .4, 1.5);
  }
.data .pushable:active .shadow {
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
    transition: -webkit-transform 34ms;
    transition: transform 34ms;
    transition: transform 34ms, -webkit-transform 34ms;
  }
.data .pushable:focus:not(:focus-visible) {
    outline: none;
  }
.data .chartContainer {
    margin-bottom: 3.5rem;
    transition: -webkit-transform 450ms;
    transition: transform 450ms;
    transition: transform 450ms, -webkit-transform 450ms;
  }

.data .chart {
    /*animation: 2.2s ease-out 0s 1 warpIn;*/
    transition: 450ms;
  }

.data .card-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
    font-size: 1.7rem;
    list-style-type: none;
    text-align: center;
  }

.data .card-item {
    margin: 1rem 0rem;
    background-color: whitesmoke;
  }

.data .card-container{
    position: fixed;
    align-self: flex-end;
    margin: 2rem;
    width: 10%;
  }

.data .return {
    margin-top: 2.5rem;
  }

.table table {
    border-collapse: collapse;
    background-color: white;
    color: black;
    font-size: 2.8rem;
    width: 100%;
    min-width: 400px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
  }

.table thead {
    background: rgba(8, 23, 56, 0.822);
    color: #ffffff;
    text-align: center;
  }

.table th, .table td {
    padding: 12px 15px
  }

 .table tbody tr {
    text-align: center;
  }

.table tbody tr:nth-of-type(even) {
    background-color: #f3f3f3;
}

