.navbar {
    background-color: #8EC5FC;
    background-image: linear-gradient( 109.6deg,  rgba(39, 143, 255, 0.795) 11.2%, rgba(98,113,255,0.78) 100.2% );
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
    flex-basis: auto;
}

.navbar h1 {
    color: #f5f5f5;
    margin-left: 25%;
}

input {
    width: 10rem;
    height: 2rem;
    border-radius: 2rem;
    border-width: 0;
    text-align: center;
    margin-left: auto;
    margin-right: 3%;
    outline-width: 0;
}

.menu-bars {
    margin-left: 2rem;
    font-size:  2rem;
    background: none;
}

.nav-menu{
    /*background: rgba(171, 210, 238, 0.8);*/
    background-color: #8EC5FC;
    background-image: linear-gradient( 109.6deg,  rgba(39, 143, 255, 0.8) 11.2%, rgba(98,113,255,0.78) 100.2% );
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%; 
    transition: 850ms;
    z-index: 1;
}

.nav-menu.active {
    left: 0;
    transition: 500ms;
}

.nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}
.nav-text a:hover {
    background-color: #1a83ff;
    transition: 400ms;
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-image: rgba(39,142,255,1);
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span {
    margin-left: 16px;
}