.home {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  color: rgb(116, 153, 170);
  background-image: linear-gradient( 109.6deg,  rgba(39,142,255,1) 11.2%, rgba(98,113,255,0.78) 100.2% );
  font-size: 1.2rem;
  height: max-content;
  width: fit-content;
}

.sectionTitle {
  color: #ffffff;
}

.home .homeNav {
    display: flex;
    flex-direction: column;
    align-self: flex-end;
    margin-right: 5%;
    margin-top: 3%;
    position: fixed;
    z-index: 1;
}

.home .homeNav-menu {
  list-style-type: none;
  background-color: #ffffff;
  width: 125%;
  border-radius: 25px
}

.home .homeNav-item {
    display: flex;
    justify-content: center;
    color: rgb(98, 128, 146);
    font-size: 1.3rem;
    padding: 0.2rem;
  }

  .home #bottomItem{
    border-radius: 0 0 25px 25px;
  }
  
  .home .menuHeader{
    background-color: #86c2d1;
    color: rgb(255, 255, 255);
    border-radius: 20px 20px 0 0;
  }
  
  .home .homeNav-link{
    text-decoration: none;
    color: rgb(86, 118, 155);
  }
  
  :not(.menuHeader).homeNav-item:hover{
    background-color: #65caf1;
    transition: 350ms;
  }
  
  .home h1{
    margin-top: 1.5rem;
    margin-bottom: 2rem;
  }
  
  .home p{
    margin-bottom: 3rem;
    text-align: center;
    line-height: 2.3rem;
  }
  
  .home .home_map{
    -webkit-box-shadow: 10px 10px 36px -5px rgba(120,145,184,1);
    -moz-box-shadow: 10px 10px 36px -5px rgba(120,145,184,1);
    box-shadow: 10px 10px 36px -5px rgba(120,145,184,1);
    height:70vh;
    width: 60vw;
    margin-top: 3rem;
    border-radius: 25px;
  }

  .home .home_map, .home .textBox, .home .homeNav-menu {
    transition: 500ms
  }

  .home .home_map:hover, .home .textBox:hover, .home .homeNav-menu:hover {
    -webkit-box-shadow: 10px 10px 36px -5px rgb(56, 84, 128);
    -moz-box-shadow: 10px 10px 36px -5px rgb(56, 84, 128);
    box-shadow: 10px 10px 36px -5px rgb(56, 84, 128);
    transition: transform 200ms;
    transform: scale(1.005)
  }

  .home .segments{
    display: flex;
    width: 100%;
    flex-direction: column;
  }
  
  .home .container{
    display: flex;
    flex-direction: column;
    align-self: center;
    align-items: center;
    width: 50%;
    margin-top: 2rem;
    margin-bottom: 2rem;
    padding-bottom: 5rem;
    border-bottom: dashed whitesmoke 5px;
  }
  
  .home .homeLink{
    color: #4e73b8;
    text-decoration: none;
    font-style: bold; 
  }
  
  .home .homeLink:hover{
    color: #359ebe;
    transition: 400ms
  } 
  
  .home .textBox{
    padding-right: 1.5rem;
    padding-left: 1.5rem;
    padding-top: 1.5rem;
    margin-bottom: 1rem;
    background-color: rgb(255, 255, 255); /*rgb(153, 189, 206)*/
    border-radius: 1.5rem;
    min-width: 450px;
  }
  
  .home .sectionTitle{
    margin-bottom: 3rem;
  }
  
  .home .dataTableContainer {
    margin-top: 5rem;
    z-index: 0;
    width: 60vw;
    max-height: max-content;
  }

  @media only screen and (max-width: 1000px) {
    .home .homeNav {
      position: relative;
      align-items: center;
      margin: 0;
      margin-top: 3%;
      margin-left: auto;
      margin-right: auto;
      width: fit-content;
      z-index: 0;
    }

    .home .homeNav-item {
      font-size: 1.3rem;
    }
  
    .home #special {
      margin-bottom: 4rem;
    }
  
    .home .home_map {
      width: 90vw;
    }

    .home .dataTableContainer {
      margin-top: 5rem;
      z-index: 0;
      width: 95vw;
      max-height: max-content;
    }
  }